import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isShow: false
  },
  mutations: {
    changeShow(state) {
      state.isShow = !state.isShow
      console.log(state.isShow)
    }
  }
})



